<template>
  <div class="body">
    <div class="section-one">
            <div class="my-photo">
                <div class="photo">
                    <img :src="this.$store.state.user_informations.photo"  alt=""  @click="this.$router.push({ name: 'HomePage' })">
                </div>
            </div>
            <div class="name-link">
                <h3>{{this.$store.state.user_informations.name}}</h3>
                <h4 v-if="this.$store.state.user_informations.show_descraption">{{this.$store.state.user_informations.descraption}}</h4>
            </div>
        </div>
  </div>
</template>

<script>

import { defineComponent } from 'vue';
import _axios from '../axios'
export default defineComponent({
    name: 'ProtfolioPage',
    data: () => ({
 
    }),
    methods:{
        get_data(){
            this.user_informations = JSON.parse(JSON.stringify(this.$store.state.user_informations))
        },
        edit_name(){
            if(this.user_informations.name.length > 0 ){
                var pyload = {name:this.user_informations.name}
                var headers = {'Authorization': `token ${window.localStorage.token}`}
                _axios.put('/api/edit_name',pyload,{headers:headers})
                .then(() => {
                    this.$store.dispatch('user_info')
                    .then( ()  => {
                        this.get_data()
                        this.response_message = 'The name has been modified successfully'
                        this.response_type = 'true'
                        document.getElementById('accoun_modal_btn').click()
            })
                })
            }
        },
    },
    components: {
    },
    created(){
        this.$store.dispatch('protifolio', this.$route.params.Protfolio)
        .then( result  => {
            result
            this.get_data()
        })
    }
  });
  </script>


<style scoped>
.body{
  background-color: #f6f6f6 ;
  height: 100%;
}
.my-photo{
  display: flex;
  justify-content: center;
  padding: 32px;
}
.photo{
  width: 120px;
  height: 120px;
  border-radius: 50%;
  background-color: white;
  box-shadow: 1px 1px 5px 0px rgba(000, 000, 000, 0.1);
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}
.photo img{
  width: 100%;
  width: 100%;
  border-radius: 50%;


}
.add-photo-btn{
  cursor:pointer;
  background-color: #f58634;
  opacity: 0.5;
  width:40%;
  height:40%;
  border-radius: 50%;
  position: absolute;
  top: 50%;  
  left: 50%; 
  transform: translate(-50%, -50%);
  transition: 0.2s;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
}
.add-photo-btn:hover{
  opacity: 0.7;
  width:50%;
  height:50%;
}
.add-photo-btn i::before{
  opacity: 0.7;
  font-size:medium;
  transition: 0.2s;
}
.add-photo-btn:hover i::before{
  font-size: larger;
}
.name-link{
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.name-link h3{
  font-weight: 500;
  font-size: xx-large;
}
.name-link h4{
font-weight: 400;
}





</style>